import loFind from 'lodash/find';
import wpMetaParser from "./wpMetaParser";
import UiPanel from "@/components/UI/Panel";
import UiBox from "@/components/UI/Box";
import UiHeading from "@/components/UI/Heading";
export default {
  name: "WineStats",
  props: {
    title: {
      type: String
    },
    fields: {
      type: [Object, Array]
    },
    category: {
      type: [Object, Array]
    }
  },
  components: {
    UiHeading: UiHeading
  },
  methods: {
    wpMetaParser: wpMetaParser
  },
  computed: {
    metas: function metas() {
      return wpMetaParser(this.fields);
    },
    vineyard: function vineyard() {
      // return "Vineyard Name"
      var match = function match(obj) {
        return JSON.stringify(obj).indexOf('vineyard') > 0;
      },
          entry = loFind(this.category, match);

      return !entry ? false : entry.name || entry['#CDATA-SECTION'] || entry;
    },
    hold: function hold() {
      if (!this.metas.length) return;
      var today = new Date().getFullYear(),
          dates = this.metas.WpbDrinkwindow.split(/-|_|–|\/|:/g);
      dates = dates.map(function (y) {
        return parseInt(y);
      });
      if (dates.length !== 2) return 1;
      return 1 - (dates[1] - today) / (dates[1] - dates[0]);
    }
  }
};