import UiBox from '.';
import fallback_img from '@/assets/mock/harvest.png';
/**
 * A flexbox-based layout component for use within a `<UiPanel/>` wrapper.
 */

export default {
  name: "UiBoxImage",
  props: {
    img: {
      type: [String, Object],
      default: fallback_img
    }
  },
  components: {
    UiBox: UiBox
  },
  computed: {
    src: function src() {
      return "url(".concat(this.img, ")");
    }
  }
};